.forms {
  &__input {
    width: 469px;
    height: 74px;
    padding-left: 37px;
    padding-right: 76px;
    color: #727272;
    background-color: #fff;
    font-size: 20px;
    line-height: 58px;
    border: 1px solid #e0e0e0;
    border-radius: 37px;
  }
  &__btn {
    height: 74px;
    margin-left: -70px;
    padding: 0 26px;
    color: #fff;
    background-color: #d31e15;
    font-size: 18px;
    line-height: 58px;
    text-transform: uppercase;
    border: 0 none;
    border-radius: 37px;
    cursor: pointer;
    transition: all $mid;
    &:hover {
      background-color: #e41703;
    }
  }
}
