.btn {
  display: inline-block;
  vertical-align: middle;
  font-family: $sans-serif;
  text-align: center;
  white-space: nowrap;
  border: 0 none;
  user-select: none;
  cursor: pointer;
  transition: all $mid;

  &--default {
    width: 262px;
    height: 73px;
    color: #565656;
    font-size: 27px;
    font-weight: 700;
    line-height: 73px;
    text-transform: uppercase;
    border: 1px solid #bf0411;
    border-radius: 37px;
    @media (min-width: 1024px) {
      width: 100%;
      max-width: 155px;
      height: 43px;
      font-size: 16px;
      line-height: 43px;
      border-radius: 23px;
    }
    &:hover,
    &.btn--active {
      color: $white;
      background-color: #b5231c;
    }
  }

}
